.todo-item-container {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  flex: auto;
}

.todo-item-list {
  display: flex;
  align-items: left;
  flex-direction: column;
}

.cancel-button {
  margin-left: 20px !important;
}

.modal-inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -50%);
  width: 400px;
  background-color: #fff;
  border: 1px solid #000;
  padding: 20px;
}

.modal-bottom-container {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  margin-top: 20px !important;
}
